.container_profile{
    margin-top: 40px;
}

.profile{
    background-color: #27292a;
    border-radius: 23px;
    padding: 60px;
}

.main_profil{
    padding: 30px;
    border-radius: 23px;
    background-color: #1f2122;
}

.profile .top{
    display: flex;
    gap: 20px;
    margin-bottom: 30px;
}

.profile .top > div{
    width: calc(100% /3);
}

.profile .top .img img{
    width: 100%;
    border-radius: 23px;
}

.profile .top .info{
    background-color: #27292a;
    border-radius: 23px;
}

.profile .description .offline{
    display: inline-block;
    background-color: #e75e8d;
    color: white;
    padding: 8px 20px;
    margin-bottom: 15px;
    border-radius: 25px;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.5px;
}

.profile .description h4{
    color: white;
    font-size: 24px;
    margin-bottom: 20px;
}

.profile .description p{
    font-size: 15px;
    color: #666;
    line-height: 30px;
}

.profile .description .button{
    margin-top: 25px;
}

.profile .description .button a{
    padding: 12px 30px;
    border: 1px solid #ec6090;
    color: #ec6090;
    border-radius: 23px;
    font-size: 14px;
}

.profile .info ul{
    padding: 30px;
}

.profile .info ul li{
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #666;
    color: #666;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    width: 100%;
}

.profile .info ul li span{
    color: #ec6090;
}

.profile .info ul li:last-child{
    margin: 0;
    padding: 0;
}

.profile .bottom{
    border-top: 2px solid #666;
}

.profile .bottom h3{
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 34px;
    color: #ec6090;
}

.profile .bottom h3 em{
    font-style: normal;
    color: white;
    text-decoration: underline;
}

.profile .bottom .container_clips{
    display: flex;
    gap: 25px;
    margin-bottom: 30px;
}

.profile .container_clips .clip{
    padding: 30px 15px;
    width: 25%;
    background-color: #27292a;
    border-radius: 23px;
}

.container_clips .clip img{
    width: 100%;
    border-radius: 23px;
    margin-bottom: 5px;
}

.container_clips .clip .clip_info{
    display: flex;
    justify-content: space-between;
    font-size: 15px;
}

.clip .clip_info{
    color: white;
}

.clip .clip_info i{
    color: #ec6090;
}


.bottom .button{
    display: flex;
    justify-content: center;
    align-items: center;
}

.bottom .button a{
    background-color: #ec6090;
    color: white;
    padding: 12px 30px;
    font-size: 14px;
    letter-spacing: 0.5px;
    display: inline-block;
    border-radius: 25px;
}