.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 12px;
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
}

.search_input input{
    background-color: #27292a;
    color: #666;
    height: 46px;
    padding: 0 15px 0 45px;
    border-color: transparent;
    border-radius: 23px;
    font-size: 14px;
    font-weight: 400;
}

.nav{
    display: flex;
    gap: 30px;
}

.nav .item{
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav .item a{
    color: #666;
    font-size: 14px;
    font-weight: 400;
}

.nav .profile{
    background-color: #27292a;
    border-radius: 23px;
    padding: 8px 10px 8px 20px;
}

.nav .profile a{
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav .profile .img_profil{
    max-width: 30px;
    border-radius: 50%;
    margin-left: 5px;
}