*{
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  background-color: #1f2122;
}

.App {
  margin: 15px 0;
}

ul {
  list-style-type: none;
}

a{
  text-decoration: none;
  color: black;
}

.container{
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 12px;
}

.row{
  display: flex;
  flex-wrap: wrap;
}

.overflow_hidden{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}