.container_details{
    margin-top: 40px;
}

.details{
    background-color: #27292a;
    border-radius: 23px;
    padding: 60px;
}

.details .images{
    width: 100%;
    display: flex;
    gap: 20px;
}

.images .col_1-3{
    width: 33%;
}

.images .col_2-3{
    width: 67%;
}

.images img{
    width: 100%;
    border-radius: 23px;
}

.title{
    margin: 60px 0;
    text-align: center;
}

.title h2{
    font-size: 45px;
    color: white;
}

.game_details .col_1{
    width: 100%;
}

.game_details .content{
    background-color: #1f2122;
    padding: 30px;
    border-radius: 29px;
}

.game_details .infos{
    display: flex;
    height: 100px;
    gap: 20px;
}

.game_details .infos .info{
    width: 50%;
    background-color: #27292a;
    border-radius: 23px;
    display: flex;
    justify-content: space-between;
    padding: 30px;
}

.details .infos .info_right ul{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.details .info_right ul li{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.details .info_right ul li i{
    margin-bottom: 10px;
}

.details .info_left ul li{
    display: flex;
    justify-content: center;
    align-items: center;
}

.details .info h4{
    color: white;
}

.details .info span{
    color: #666;
}

.details .info_left ul li i{
    margin-right: 5px;
}

.details ul li{
    color: white;
    font-size: 14px;
}

.details ul i{
    color: #ec6090;
    font-size: 14px;
}

.details ul .fa-star{
    color: yellow;
}

.game_details .img_details{
    display: flex;
    margin: 30px 0;
    gap: 20px;
}

.game_details .img_details .img{
    width: calc(100% / 3);
}

.game_details .img_details img{
    width: 100%;
    border-radius: 23px;
}

.game_details .description p{
    font-size: 15px;
    color: #666;
    line-height: 30px;
}

.game_details .button{
    margin-top: 30px;
    border: 1px solid #ec6090;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
}

.game_details .button a{
    margin: 12px 30px;
    color: #ec6090;
    font-size: 14px;
}

.container_related{
    margin-top: 60px;
    padding: 30px;
    background-color: #1f2122;
    border-radius: 23px;
}

.related h3{
    font-size: 34px;
    color: #ec6090;
}

.related h3 em{
    color: white;
    text-decoration: underline;
    font-style: normal;
}

.container_related img{
    width: 80px;
    border-radius: 25px;
}

.container_items{
    display: flex;
    gap: 20px;
    padding: 30px 0;
    border-bottom: 1px solid black;
}

.container_related .item{
    display: flex;
    justify-content: space-between;
    width: 50%;
    gap: 10px;
}

.container_related .item h4{
    color: white;
}

.container_related .item span{
    color: #666;
}

.container_related .item .desc{
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}

.container_related .item ul li i{
    border-right: 5px;
}