.container_browse{
    margin-top: 40px;
}

.browse{
    background-color: #27292a;
    border-radius: 23px;
    padding: 60px;
}

.browse .main_container{
    display: flex;
    gap: 20px;
}

.container_games{
    background-color: #1f2122;
    width: 67%;
    padding: 30px;
    border-radius: 25px;
}

.container_games .games{
    display: flex;
    gap: 30px;
}

.container_games h2{
    margin-bottom: 30px;
    font-size: 34px;
    color: #ec6090;
}

.container_games h2 em{
    font-style: normal;
    color: white;
    text-decoration: underline;
}

.game{
    width: 50%;
}

.game img{
    width: 100%;
    border-radius: 23px;
}

.game .game_info{
    display: flex;
    justify-content: space-between;
}

.game_info .game_left h3{
    color: white;
    font-size: 15px;
}

.game_left span{
    color: #666;
    font-size: 15px;

}

.game .game_info ul li i{
    margin-right: 5px;
}

.game .game_info ul li{
    color: white;
    font-size: 14px;
    display: flex;
    justify-content: flex-end;
}

.game .game_info ul i{
    color: #ec6090;
    font-size: 14px;
}

.game .game_info ul .fa-star{
    color: yellow;
}

.container_downloaded{
    background-color: #1f2122;
    width: 33%;
    padding: 30px;
    border-radius: 25px;
}

.browse h2{
    margin: 60px 0;
    color: #ec6090;
    text-align: center;
    font-size: 34px;
}

.browse h2 em{
    color: white;
    font-style: normal;
    text-decoration: underline;
}

.container_start .items{
    display: flex;
    gap: 24px;
}

.container_start .item{
    width: calc(100% /3);
    padding: 30px;
    border: 1px solid #666;
    border-radius: 23px;
}

.container_start .item p{
    font-size: 15px;
    line-height: 30px;
    color: #666;
}

.container_start .item img{
    border-radius: 50%;
}

.container_start .item h4{
    font-size: 20px;
    color: white;
    font-weight: 600;
    margin-bottom: 15px;
    margin-top: 20px;
}

.container_start .button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.container_start .button a{
    color: white;
    background-color: #ec6090;
    border-radius: 23px;
    padding: 12px 30px;
    display: inline-block;
    font-size: 14px;
}