.container_library{
    margin-top: 60px;
    background-color: #1f2122;
    border-radius: 23px;
}

.library{
    padding: 30px;
    position: relative;
}

.library .title_library{
    font-size: 34px;
    color: #ec6090;
    margin-bottom: 30px;
    margin-top: 0;
    margin-left: 12px;
}

.library .title_library em{
    text-decoration: underline;
    color: white;
    font-style: normal;
}

.library .item{
    width: 100%;
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #27292a;
}

.library .item ul{
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-left: 20px;
}

.library .item li{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.library .item ul h4{
    color: white;
    font-size: 15px;
    font-weight: 700;
    margin-bottom: 5px;
}

.library .item ul span{
    color: #666;
    font-size: 14px;
    font-weight: 400;
}

.library .container_button{
    width: 100%;
    text-align: center;
}

.library .button {
    margin-bottom: -53px;
}

.library .button a {
    font-size: 14px;
    color: #fff;
    background-color: #e75e8d;
    padding: 12px 30px;
    display: inline-block;
    border-radius: 25px;
    font-weight: 400;
}

.library .button_download a{
    font-size: 14px;
    color: grey;
    padding: 12px 30px;
    display: inline-block;
    border-radius: 25px;
    font-weight: 400;
    border: 2px solid grey;
}
