.container_accueil{
    margin-top: 40px;
}

.accueil{
    background-color: #27292a;
    border-radius: 23px;
    padding: 60px;
}

.accueil .main_banner{
    background-image: url('/public/images/banner-bg.jpg');
    min-height: 380px;
    background-position: center;
    background-size: cover;
    padding: 80px 60px;
    border-radius: 23px;
}

.main_banner .col{
    width: 58.333%;
}

.header-text h6{
    font-size: 20px;
    color: #fff;
    font-weight: 400;
    margin-bottom: 25px;
    margin-top: 0;
}

.header-text h4{
    margin: 0;
    font-size: 45px;
    text-transform: uppercase;
    margin-bottom: 25px;
    color: white;
    font-weight: 700;
    line-height: 54px;
}

.header-text h4 em{
    font-style: normal;
    color: #ec6090;
}

.main-button{
    height: 100%;
}

.header-text .main-button a{
    font-size: 14px;
    font-weight: 400;
    padding: 12px 30px;
    border-radius: 25px;
    color: white;
    background-color: #ec6090;
    display: inline-block;
}