.container_mostpopular{
    margin-top: 60px;
    background-color: #1f2122;
    border-radius: 23px;
}

.mostpopular{
    padding: 20px;
    position: relative;
}

.mostpopular .title_mostpopular{
    font-size: 34px;
    color: #ec6090;
    margin-bottom: 30px;
    margin-top: 0;
    margin-left: 12px;
}

.mostpopular .title_mostpopular em{
    text-decoration: underline;
    color: white;
    font-style: normal;
}

.mostpopular .item_mostpopular{
    padding: 30px 15px;
    background-color: #27292a;
    margin-bottom: 30px;
    border-radius: 23px;
}

.mostpopular .col{
    width: 25%;
    padding: 0 12px;
}

.mostpopular img{
    width: 100%;
    border-radius: 23px;
}

.item_mostpopular .info{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.info ul{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding-left: 0;
}

.item_mostpopular h4{
    color: white;
    font-weight: 700;
    font-size: 15px;
}

.item_mostpopular h4 p{
    color: #666;
    font-weight: 400;
    font-size: 15px;
}

.item_mostpopular i{
    font-size: 14px;
}

.item_mostpopular li{
    color: white;
    font-size: 14px;
    white-space: nowrap;
}

.item_mostpopular .fa-star{
    color: yellow;
}

.item_mostpopular .fa-download{
    color: #ec6090;
}

.mostpopular .container_button{
    width: 100%;
    text-align: center;
}

.mostpopular .button {
    margin-bottom: -53px;
}

.mostpopular .button a {
    font-size: 14px;
    color: #fff;
    background-color: #e75e8d;
    padding: 12px 30px;
    display: inline-block;
    border-radius: 25px;
    font-weight: 400;
}